@import "global";

// Hero stuff
.project-hero-container
{
    padding-top: $navbar-height;
    background-color: $pink;
}

.hero-wrapper {
    max-width: $max-width-page;
    margin-left: auto;
    margin-right: auto;
    vertical-align: middle;
    height: fit-content;
    display: block;
    padding-bottom: 20px;

    .img-container {

        padding: 24px;
        width: fit-content;
        display:inline-block;
        padding-bottom: 0px;
    }

    img {
        padding: 20px 0px;
        width: 468;
        @media #{$project-page-breakpoint} {
            width: 100%;
        }
    }

    h1 {
        @include font-azo-black;
        font-size: 40px;
        color: $dark-purple;
        margin: 0px;
        padding: 3px 0px;
    }

    h2 {
        @include font-azo-bold;
        font-size: 18px;
        color: $dark-purple;
    }

    h3 {
        @include font-azo-normal;
        color: $dark-purple;
        font-size: 18px;
        padding: 0;
        margin: 0px;
    }

    h4 {
        @include font-azo-black;
        color: $peach;
        font-size: 13px;
    }

    .hero-container {
        padding-top: 110px;
        padding-right: 15px;
        width: fit-content;
        display:inline-block;
        float: right;


        width: calc(100vw - 580px);
        max-width: $max-width-page - 600px;


        @media #{$project-page-breakpoint} {
            width: fit-content;
            max-width: none;
            float:none;
            padding: 24px;
            padding-top: 0px;
            margin-right: 20px;
        }

    }
}

// Project description stuff
.copy-wrapper
{
    max-width: $max-width-page;
    margin-left: auto;
    margin-right: auto;
    vertical-align: middle;
    height: fit-content;
    padding: 24px;
    padding-top: 0px;

    iframe {
        @media #{$small} {
            height: 290px;
        }
        @media #{$medium} {
            height: 480px;
        }
        @media #{$large} {
            height: 650px;
        }
      
    }

    p {
        @include font-azo-normal;
        color: $dark-purple;
        font-size: 16px;
    }

    h1 {
        @include font-azo-bold;
        color: $peach;
        font-size: 24px;
        padding-top: 20px;

    }

    h2 {
        @include font-azo-bold;
        color: $dark-purple;
        font-size: 20px;
    }

    img {
        width: 100%;
        vertical-align: middle;
    }

    a {
        text-decoration: none;
        color: #3e94db;
        transition: all 0.3s;
    }

    a:hover{
        color: #365699
    }

    .gif {
        max-width: 500px;
    }

}